import * as React from 'react';
import * as Modal from 'react-modal';
import { HippoButton } from '@components/HippoFields';
import localizedStrings from '@root/translations.1/localizedStrings';

export interface IOwnProps {
    isOpen: boolean;
    footer?: React.ReactNode;
    onClose?: () => void;
}

export default class MessageDialog extends React.Component<IOwnProps> {
    private modalStyle = {
        content: {
            margin: 'auto',
            position: 'static'
        },
        overlay: { display: 'flex' },
    };

    public render() {
        const {
            isOpen,
            onClose,
            children,
            footer,
        } = this.props;

        return <Modal isOpen={isOpen} ariaHideApp={false} style={this.modalStyle}>
            {children}
            {footer || <HippoButton label={localizedStrings.ok} onClick={onClose} />}
        </Modal>;
    }
}


