import * as superagentPromise from 'superagent-promise';
import * as superagent from 'superagent';

const agent = superagentPromise(superagent, Promise);
const returnBody = (res) => res.body;
const returnText = (res) => res.text;
const okStatusCodes = (res) => {
    return res.status < 400 || [422].indexOf(res.status) >= 0;
};

const get = (url, params = {}, options: any = {}) => {
    return agent.get(url).query(params).then(options.returnText ? returnText : returnBody);
};

export interface IHttpRequestParams {
    params?: any;
    type?: string;
}

export interface IPostOptions extends IHttpRequestParams {
    applyOnRequest?: (request) => any;
    noAuthHeader?: boolean;
    returnRaw?: boolean;
}

const post = (url, options: IPostOptions = {}) => {
    if (!options.applyOnRequest) {
        options.applyOnRequest = (request) => {
            return request.type(options.type || 'json').send(options.params);
        };
    }

    return options.applyOnRequest(agent.post(url)).ok(okStatusCodes).then(returnBody);
};

const put = (url, options: IHttpRequestParams = {}) => {
    return agent.put(url).type(options.type || 'json').send(options.params).then(returnBody);
};

const deleteFn = (url, options: IHttpRequestParams = {}) => {
    return agent.del(url).type(options.type || 'json').send(options.params).then(returnBody);
};

export default {
    get,
    post,
    put,
    ['delete']: deleteFn
};


