import * as React from 'react';
import ValidationDialog, { IValidationInfo } from '@components/Dialogs/ValidationDialog';
import { HippoButton, HippoFileSelector } from '@components/HippoFields';
import * as httpCodes from '@constants/httpCodes';
import { getBulkImportTemplate, postBulkImportCsv } from '@api/dataImportApi';
import {
    createStyles,
    ExpansionPanel,
    ExpansionPanelDetails,
    ExpansionPanelSummary,
    WithStyles,
    withStyles,
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import localizedStrings from '@root/translations.1/localizedStrings';
import { LoadingDialog } from 'hippo-components';
import { toast } from 'react-toastify';
import ValidationError from './ValidationError';

const styles = createStyles({
    expansionPanel: {
        backgroundColor: '#FFFFFF',
        minHeight: '3rem',
        margin: '3px',
        padding: '0 3rem',
        fontSize: '18px',
        boxShadow: 'none',
        '&:before': {
            backgroundColor: '#FFFFFF',
        },
    },
    expansionPanelHeader: {
        padding: 0,
    },
    expansionContent: {
        padding: 0,
        width: '100%',
        display: 'inline-flex',
        justifyContent: 'space-between',
    },
    text: {
        flexGrow: 1,
        margin: 'auto',
        fontSize: '14px',
    },
    button: {
        marginLeft: '20px',
        width: 'auto',
    },
});

interface IImportDataBoxProps extends WithStyles<typeof styles> {
    open: boolean;
    label: string;
    importName: string;
    description: string;
    onToggle: (boxName: string) => void;
}

interface IImportDataBoxState {
    loading: boolean;
    validation: IValidationInfo;
}

class ImportDataBox extends React.Component<IImportDataBoxProps, IImportDataBoxState> {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            validation: null,
        };

        this.downloadTemplate = this.downloadTemplate.bind(this);
        this.uploadFile = this.uploadFile.bind(this);
        this.onValidationDialogClose = this.onValidationDialogClose.bind(this);
    }

    public render() {
        const {
            classes,
            open,
            label,
            importName,
            description,
            onToggle,
        } = this.props;
        const {
            loading,
            validation,
        } = this.state;

        return (
            <>
                <LoadingDialog open={loading} />
                <ExpansionPanel className={classes.expansionPanel} expanded={open} onChange={() => onToggle(importName)}>
                    <ExpansionPanelSummary
                        className={classes.expansionPanelHeader}
                        expandIcon={<ExpandMoreIcon />}
                    >
                        <span>{label}</span>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails className={classes.expansionContent}>
                        <span className={classes.text}>{description}</span>
                        <HippoFileSelector
                            className={classes.button}
                            onFilesSelected={(files: FileList) => this.uploadFile(files[0])}
                            accept=".csv"
                            label={localizedStrings.import}
                        />
                        <HippoButton
                            label={localizedStrings.downloadTemplate}
                            onClick={this.downloadTemplate}
                            addClass={classes.button}
                        />
                    </ExpansionPanelDetails>
                </ExpansionPanel>
                {validation && (
                    <ValidationDialog
                        validation={validation}
                        onValidationDialogClose={this.onValidationDialogClose}
                    />
                )}
            </>
        );
    }

    private async downloadTemplate() {
        const { response, error } = await getBulkImportTemplate(this.props.importName);
        if (!response) {
            toast.error(localizedStrings.downloadTemplateError);
            return;
        }
        const blob = new Blob([response], {
            type: 'text/csv;charset=utf8;'
        });
        const element = document.createElement('a');
        document.body.appendChild(element);
        element.download = `${this.props.importName}Template.csv`;
        element.href = window.URL.createObjectURL(blob);
        element.style.display = '';
        element.click();
    }

    private async uploadFile(file: File) {
        const reader = new FileReader();
        reader.onloadend = async () => {
            this.setState({ loading: true });

            const { response, error } = await postBulkImportCsv(this.props.importName, reader.result);
            this.setState({ loading: false });
            if (error && error.status !== httpCodes.UNPROCESSABLE_ENTITY) {
                toast.error(localizedStrings.importError);
                return;
            }

            this.handleValdiation(response, error);
        };
        reader.readAsText(file);
    }

    private handleValdiation(response, error) {
        if (error) {
            const validation = error.response.body;
            this.setState({
                validation: {
                    title: validation.Error,
                    description: validation.Description,
                    unprocessableEntities: {
                        headers: validation.UnprocessableEntities.Headers,
                        rows: validation.UnprocessableEntities.Rows,
                    },
                },
            });
            return;
        }

        if (response && response.Details) {
            this.setState({
                validation: {
                    title: response.Error,
                    errors: response.Details.map(v =>
                        new ValidationError(v.Property, v.Error)),
                }
            });

            return;
        }

        if (response && response.Error) {
            this.setState({
                validation: {
                    title: response.Error,
                }
            });

            return;
        }

        toast.success(localizedStrings.uploadSuccess);
    }

    private onValidationDialogClose() {
        this.setState({ validation: null });
    }
}

export default withStyles(styles)(ImportDataBox);



