import * as React from 'react';
const style = require('./HippoButton.scss');

export interface IOwnProps{
    addClass?: string;
    hide?: boolean;
    onClick?: () => void;
    label: string;
    style?: any;
    type?: string;
}

export default function HippoButton(props: IOwnProps) {
    let styleClass = style.default;
    if (props.addClass) {
        styleClass += ' ' + props.addClass;
    }
    if (props.hide) {
        return null;
    }

    return <button onClick={props.onClick} className={styleClass} style={props.style} type={props.type || 'button'}>{props.label}</button>;
}


