import * as React from 'react';
import { IntlProvider, addLocaleData } from 'react-intl';

import * as frI18n from 'react-intl/locale-data/fr';
import * as esI18n from 'react-intl/locale-data/es';
const englishMessages = require('@translations/locale-en.json');
import 'intl';

const ALL_MESSAGES = {
    en: englishMessages
};
const DEFAULT_LANGUAGE = 'en';
addLocaleData([...frI18n, ...esI18n]);
const navigatorCasted = navigator as any;
const language = navigator.languages && navigator.languages[0] ||
    navigator.language ||
    navigatorCasted.userLanguage;

function getLocalizedMessages() {
    const langRegex = /^([a-z]{2})(\-.*)?$/;
    let genericLang;
    try { genericLang = language.match(langRegex)[1]; } catch (exception) {
        genericLang = DEFAULT_LANGUAGE;
    }
    return ALL_MESSAGES[genericLang] || ALL_MESSAGES[DEFAULT_LANGUAGE];
}

export default class CustomIntlProvider extends React.Component {
    public render() {
        return <IntlProvider locale={language} messages={getLocalizedMessages()}>
            {this.props.children}
        </IntlProvider>;
    }
}


