import * as React from 'react';
import HippoButton from '@components/HippoFields/HippoButton';
const style = require('./HippoButton.scss');

export interface IOwnProps {
    onFilesSelected: (files: FileList) => void;
    accept: string;
    label?: string;
    className?: string;
    resetSelectedFiles?: () => void;
}

export default class FileSelector extends React.Component<IOwnProps>
{
    private inputRef: HTMLInputElement;

    constructor(props: any) {
        super(props);
    }

    public render() {
        return <div className={this.props.className}>
            <HippoButton
                label={this.props.label ? this.props.label : 'Upload'}
                onClick={() => this.inputRef.click()}
                addClass={style.uploadBtn}
            />
            <input
                style={{ display: 'none' }}
                type="file"
                onChange={e => { this.props.onFilesSelected(e.target.files); this.resetFiles(); }}
                ref={e => this.inputRef = e }
                accept={this.props.accept}
            />
        </div>;
    }

    private resetFiles() {
        this.inputRef.value = null;
    }
}


