import * as React from 'react';
import ImportDataBox from '@components/ImportData/ImportDataBox';
import {
    createStyles,
    WithStyles,
    withStyles
} from '@material-ui/core';
import localizedStrings from '@root/translations.1/localizedStrings';

const styles = createStyles({
    container: {
        backgroundColor: '#EFF3F6',
        height: '100%',
        padding: '40px 20px',
    },
});

interface IImportDataState {
    openDataBoxName?: string;
}

class ImportData extends React.Component<WithStyles<typeof styles>, IImportDataState> {
    constructor(props) {
        super(props);

        this.state = {};

        this.onBoxToggle = this.onBoxToggle.bind(this);
    }
    public render() {
        const { classes } = this.props;
        const { openDataBoxName } = this.state;

        return (
            <div className={classes.container}>
                <ImportDataBox
                    open={openDataBoxName === 'equipmentTags'}
                    label={localizedStrings.importData.equipmentTagLabel}
                    importName="equipmentTags"
                    description={localizedStrings.importData.equipmentTagDescription}
                    onToggle={this.onBoxToggle}
                />
                <ImportDataBox
                    open={openDataBoxName === 'equipmentModelImages'}
                    label={localizedStrings.importData.equipmentModelImageLabel}
                    importName="equipmentModelImages"
                    description={localizedStrings.importData.equipmentModelImageDescription}
                    onToggle={this.onBoxToggle}
                />
                <ImportDataBox
                    open={openDataBoxName === 'equipmentModelSpecs'}
                    label={localizedStrings.importData.equipmentModelSpecLabel}
                    importName="equipmentModelSpecs"
                    description={localizedStrings.importData.equipmentModelSpecDescription}
                    onToggle={this.onBoxToggle}
                />
                <ImportDataBox
                    open={openDataBoxName === 'specs'}
                    label={localizedStrings.importData.specLabel}
                    importName="specs"
                    description={localizedStrings.importData.specDescription}
                    onToggle={this.onBoxToggle}
                />
                <ImportDataBox
                    open={openDataBoxName === 'documents'}
                    label={localizedStrings.importData.documentLabel}
                    importName="documents"
                    description={localizedStrings.importData.documentDescription}
                    onToggle={this.onBoxToggle}
                />
                <ImportDataBox
                    open={openDataBoxName === 'documentHistories'}
                    label={localizedStrings.importData.documentHistoryLabel}
                    importName="documentHistories"
                    description={localizedStrings.importData.documentHistoryDescription}
                    onToggle={this.onBoxToggle}
                />
                <ImportDataBox
                    open={openDataBoxName === 'workOrderHistoryDocuments'}
                    label={localizedStrings.importData.workOrderDocumentHistoryLabel}
                    importName="workOrderHistoryDocuments"
                    description={localizedStrings.importData.workOrderDocumentHistoryDescription}
                    onToggle={this.onBoxToggle}
                />
                <ImportDataBox
                    open={openDataBoxName === 'users'}
                    label={localizedStrings.importData.userLabel}
                    importName="users"
                    description={localizedStrings.importData.userDescription}
                    onToggle={this.onBoxToggle}
                />
            </div>
        );
    }

    private onBoxToggle(boxName: string) {
        this.setState(state => ({ openDataBoxName: state.openDataBoxName !== boxName && boxName }));
    }
}

export default withStyles(styles)(ImportData);



