import * as React from 'react';
import MessageDialog from './MessageDialog';
import { HippoButton } from '@components/HippoFields';
import {
    createStyles,
    WithStyles,
    withStyles
} from '@material-ui/core';
import localizedStrings from '@root/translations.1/localizedStrings';
import ValidationError from '../ImportData/ValidationError';

const styles = createStyles({
    title: {
        display: 'block',
        margin: '0 5px 20px',
    },
    description: {
        margin: '0 5px 10px',
        fontSize: '14px',
        color: 'red',
    },
    tableHeader: {
        minWidth: '100px',
        padding: '7px',
        fontSize: '14px',
        color: '#DFDFDF',
        backgroundColor: '#777777',
    },
    tableCell: {
        padding: '7px',
        fontSize: '12px',
        color: 'red',
        border: '1px solid #DCDCDC',
    },
    button: {
        width: 'auto',
        color: '#646464',
        backgroundColor: '#FFFFFF',
        border: '1px solid #7A7A7A',
        float: 'right',
        '&:hover': {
            backgroundColor: '#F0F0F0',
        },
    },
});

export interface IValidationInfo {
    title: string;
    description?: string;
    errors?: ValidationError[];
    unprocessableEntities?: {
        headers: string[];
        rows: any[];
    };
}

interface IValidationDialogProps extends WithStyles<typeof styles> {
    onValidationDialogClose: () => void;
    validation: IValidationInfo;
}

class ValidationDialog extends React.Component<IValidationDialogProps> {
    constructor(props) {
        super(props);
    }

    public render() {
        const {
            classes,
            validation: {
                title,
                description,
                errors,
                unprocessableEntities,
            },
            onValidationDialogClose,
        } = this.props;

        return (
            <MessageDialog
                isOpen={true}
                footer={<HippoButton addClass={classes.button} label={localizedStrings.close} onClick={onValidationDialogClose} />}
            >
                <strong className={classes.title}>{title}</strong>
                <p className={classes.description}>{description}</p>
                {errors && errors.map((error, key) => {
                    return (
                        <p key={key}>
                            {error.column} : {error.error}
                        </p>
                    );
                })}
                {unprocessableEntities && (
                    <table>
                        <thead>
                            <tr>
                                {unprocessableEntities.headers.map(header => (<th key={header} className={classes.tableHeader}>{header}</th>))}
                            </tr>
                        </thead>
                        <tbody>
                            {unprocessableEntities.rows.map(entity => (
                                <tr key={entity}>
                                    {unprocessableEntities.headers.map(header => <td key={header} className={classes.tableCell}>{entity[header]}</td>)}
                                </tr>
                            ))}
                        </tbody>
                    </table>
                )}
            </MessageDialog>
        );
    }
}

export default withStyles(styles)(ValidationDialog);



