import * as React from 'react';
import { NavLink, RouteComponentProps, withRouter } from 'react-router-dom';
import localizedStrings from '../../translations.1/localizedStrings';
import * as ROUTES from '@constants/routes';
import { CSSTransition } from 'react-transition-group';
const style = require('./Menu.scss');

export interface IOwnState {
    fileMenuOpen: boolean;
}

class Menu extends React.Component<RouteComponentProps<{}>, IOwnState> {
    constructor(props) {
        super(props);
        this.changeFileSubMenuState = this.changeFileSubMenuState.bind(this);

        this.state = {
            fileMenuOpen: this.isSubRoute(ROUTES.file.ROOT)
        };
    }

    public render() {
        return (
            <React.Fragment>
                <div className={style.topBar}>
                    <img src="/images/app-logo.png" className={style.logo} />
                </div>
                <div className={style.main}>
                    <div className={style.menu}>
                        <NavLink to={{ pathname: ROUTES.SSO, search: this.props.location.search }}
                            className={style.link}
                            activeClassName={style.activeLink}>{localizedStrings.ssoSettings.sso}</NavLink>
                        <p className={style.link} onClick={this.changeFileSubMenuState}>{localizedStrings.files.filesAdmin}</p>
                        <CSSTransition
                            in={this.state.fileMenuOpen}
                            unmountOnExit={true}
                            classNames={{
                                enter: style.submenuEnter,
                                enterActive: style.submenuEnterActive,
                                exit: style.submenuExit,
                                exitActive: style.submenuExitActive,
                            }}
                            timeout={500}>
                            <div className={style.submenu}>
                                <NavLink to={{ pathname: ROUTES.file.GRID, search: this.props.location.search }}
                                    className={style.link}
                                    activeClassName={style.activeLink}>{localizedStrings.files.fileGrid}</NavLink>
                                <NavLink to={{ pathname: ROUTES.file.UPLOAD, search: this.props.location.search }}
                                    className={style.link}
                                    activeClassName={style.activeLink}>{localizedStrings.files.importFiles}</NavLink>
                                <NavLink to={{ pathname: ROUTES.DATA.IMPORTS, search: this.props.location.search }}
                                    className={style.link}
                                    activeClassName={style.activeLink}>{localizedStrings.data.import}</NavLink>
                            </div>
                        </CSSTransition>
                        <NavLink to={{ pathname: ROUTES.CLIENT_TOOLS, search: this.props.location.search }}
                            className={style.link}
                            activeClassName={style.activeLink}>{localizedStrings.clientTools.title}</NavLink>
                    </div>
                    <div className={style.content}>
                        {this.props.children}
                    </div>
                </div>
            </ React.Fragment>
        );
    }

    private isSubRoute(root) {
        return this.props.location.pathname.indexOf(root) === 0;
    }

    private changeFileSubMenuState() {
        this.setState(prevState => ({ fileMenuOpen: !prevState.fileMenuOpen }));
    }
}

export default withRouter(Menu);


