import ConfigProvider from '@root/ConfigProvider';
import agent from '@root/agent';

export const getBulkImportTemplate = (name) => {
    return agent.get(`${ConfigProvider.fakeService}/v1/bulk/${name}`, [], { returnText: true })
        .then((response) => ({ response }))
        .catch(error => ({ error }));
};

export const postBulkImportCsv = (name, content) => {
    return agent.post(`${ConfigProvider.fakeService}/v1/bulk/${name}`, {
        params: content,
        returnRaw: true,
        type: 'text/csv'
    })
        .then((response) => ({ response }))
        .catch(error => ({ error }));
};


